<template>
  <div class="no-result" :style='resultYs'>
    <!--<img src="../../assets/images/public/no-collect.png"/>-->
    <img :src="src" alt="">
    <p class="title">{{title}}</p>
    <div class="btn" @click="btnClick" v-if="btnShow">{{btnText}}</div>
    <!--<router-link :to="link" v-show="relationShow" class="goback">{{tip}}</router-link>-->
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    src: {
      type: String,
      default: ''
    },
    btnText: {
      type: String,
      default: ''
    },
    absTop: {
      type: Number,
      default: 0
    },
    btnShow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {}
  },
  computed: {
    resultYs () {
      return {
        top: this.absTop ? (this.absTop / 100) + 'rem' : 0
      }
    }
  },
  methods: {
    btnClick () {
      this.$emit('handleClik')
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/mixin.scss";
  .no-result {
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    img {
      width: pxToRem(200px);
      height: pxToRem(130px);
      display: block;
      margin:0 auto;
      margin-top: pxToRem(310px);
    }
    .title {
      font-size: pxToRem(30px);
      color: #C8CBCB;
      margin: pxToRem(50px) 0  pxToRem(80px);
      text-align: center;
    }
    .btn{
      font-size: 16px;
      color: #FFFFFF;
      text-align: center;
      background: #1CD7D1;
      border-radius: 22px;
      width:140px;
      height:40px;
      line-height: 40px;
      margin:0 auto;
    }
  }
</style>
