<template>
  <div >
    <order-tab @changeTab="handleChangeTab"></order-tab>
    <div class="order-list" v-if="orderTabCur===0">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in unpayLists" class="page-infinite-listitem" :key="index">
            <base-order :item='item'></base-order>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          ...
        </p>
        <p v-show="!loading&&unpayLists.length" class="noMoreData"></p>
        <div v-show="!unpayLists.length" class="no-result-wrapper">
          <no-result :title="''" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="order-list" v-if="orderTabCur===1">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in guranteeLists" class="page-infinite-listitem" :key="index">
            <base-order :item='item'></base-order>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          ...
        </p>
        <p v-show="!loading&&guranteeLists.length" class="noMoreData"></p>
        <div v-show="!guranteeLists.length" class="no-result-wrapper">
          <no-result :title="''" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="order-list" v-if="orderTabCur===2">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in allList" class="page-infinite-listitem" :key="index">
            <base-order :item='item'></base-order>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          ...
        </p>
        <p v-show="!loading&&allList.length" class="noMoreData"></p>
        <div v-show="!allList.length" class="no-result-wrapper">
          <no-result :title="''" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import orderTab from '@/components/order/myOrderTab'
import baseOrder from '@/components/order/BaseOrder'
import noResult from '@/components/base/noResult'
import {
  mapState,
  mapGetters,
  mapMutations
} from 'vuex'

export default {
  name: 'myOrder',
  data () {
    return {
      wrapperHeight: 0,
      src: require('@/assets/images/no_order.png'),
      page: 1,
      limit: 10,
      status: '4',
      loading: false,
      allList: [],
      unpayLists: [],
      guranteeLists: []
    }
  },
  mounted () {
    this.getList()
  },
  computed: {
    ...mapState([
      'orderTabCur',
      'orderTabCurState'
    ])

  },
  methods: {
    ...mapMutations(['SET_ORDER_TAB_CUR']),
    handleChangeTab (index) {
      this.getList()
    },
    //   this.getOrderList()
    // },
    getList () {
      this.page = 1
      let postData = {
        page: this.page,
        limit: this.limit,
        status: this.orderTabCurState
      }
      this.$api.personCenter.getIndexOrderList(postData).then((res) => {
        if (res.code === 1) {
          this.loading = true
          // this.lists = res.data.dataGrid.data
          switch (this.orderTabCurState) {
            case '0':
              this.unpayLists = res.data.dataGrid.data
              break
            case '4':
              this.guranteeLists = res.data.dataGrid.data
              break
            case '':
              this.allList = res.data.dataGrid.data
              break
          }
          this._checkMore(res.data)
          // $set(this.lists, `status${this.status}`, res.data.dataGrid.data)
        }
      })
    },
    loadMore () {
      console.log(234,this.loading)
      if (!this.loading) {
        return
      }
      this.page++
      this.$api.personCenter.getIndexOrderList({
        page: this.page,
        limit: this.limit,
        status: this.orderTabCurState
        // status: this.status
      }).then(
        function (res) {

          // this.$set(this.lists, `status${this.status}`, res.data.dataGrid.data)\
          if (res.code === 1) {
            switch (this.orderTabCurState) {
              case '0':
                this.unpayLists = this.unpayLists.concat(res.data.dataGrid.data)
                break
              case '4':
                this.guranteeLists = this.guranteeLists.concat(res.data.dataGrid.data)
                break
              case '':
                this.allList = this.allList.concat(res.data.dataGrid.data)
                break
            }
          }

          this._checkMore(res.data)
        }.bind(this)
      )
        .catch(function (err) {
        })
    },
    _checkMore (data) {
      console.log('hasMore',data,data.total,this.page * this.limit,data.dataGrid.data.length)
      if ((this.page * this.limit) >= data.total) {
        console.log('nohasMore')
        this.hasMore = false
      }
    }
  },
  components: {
    noResult,
    baseOrder,

    orderTab,
    // orderList

  }
}
</script>

<style  lang="scss">
  @import "@/assets/styles/mixin.scss";
  .page-infinite-loading{
    text-align: center;
  }
  .mint-spinner-fading-circle{
    margin:0 auto;
  }
  .noMoreData{
    margin-top: pxToRem(30px);
    color: #A5A8A8;
    text-align: center;
  }
  .page-infinite-listitem {
    margin-bottom: pxToRem(20px);
  }

</style>
