<template>
  <div class="tabs " >
    <!--<div class="tab-item fl" v-for="(tab,index) in tabs" v-text="tab.tabtxt" :class="{active: orderTabCur===index}"-->
         <!--@click="handleClick(tab,index)"></div>-->
    <!--<div class="tab-item fl" v-for="(tab,index) in tabs" v-text="tab.tabtxt" :class="{active: tab.active}"-->
         <!--@click="handleClick(tab)"></div>-->
    <div class="tab-item" @click="setCur(0,'0')" :class="{active: orderTabCur==0}">
    <span></span>
    </div>
    <div class="tab-item" @click="setCur(1,'4')" :class="{active: orderTabCur==1 }">
    <span></span>
    </div>
    <div class="tab-item" @click="setCur(2,'')" :class="{active: orderTabCur==2}">
    <span></span>
    </div>
  </div>
</template>
<script>
import {
  mapGetters, mapMutations
} from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'orderTabCur'
    ])
  },

  methods: {
    setCur (index, status) {
      this.SET_ORDER_TAB_CUR(index)
      this.SET_ORDER_TAB_CUR_STATE(status)
      this.$emit('changeTab')
    },
    ...mapMutations(['SET_ORDER_TAB_CUR', 'SET_ORDER_TAB_CUR_STATE'])

  }
}
</script>

<style lang="scss" scoped="scoped">
  @import "../../assets/styles/mixin.scss";

  .tabs {
    height: pxToRem(82px);
    line-height: pxToRem(82px);
    background: #FFFFFF;
    display: flex;
    justify-content: space-between;
    border-bottom: pxToRem(2px) solid #EDEDED;
    .tab-item {
      white-space: nowrap;
      width: pxToRem(250px);
      text-align: center;
      font-size: pxToRem(30px);
      color: $color-text;
      box-sizing: border-box;
      &.active {
        color: $color-text;
        border-bottom: pxToRem(4px) solid $color-active-text;
      }
    }
  }
</style>
